<template>
  <v-container fluid pt-0>
    <v-layout justify-center>
      <v-flex xs12 sm12 md9 lg6>
        <v-flex mb-3 bw>
          <v-text-field
            v-model="searchAntimicrobe"
            label="Search Antibiotics/Antimicrobials"
            placeholder="Start typing to search"
            :append-icon="inputIcon"
            @click:append="searchAntimicrobe = ''"
            outline
          ></v-text-field>
        </v-flex>
        <v-layout column>
          <v-flex
            v-for="antimicrobial in filteredAntimicrobials"
            :key="antimicrobial.id"
            mb-3
            pa-2
            card
            @click="goto(antimicrobial.antimicrobialName)"
            pcoh
          >
            <v-layout justify-space-between align-center>
              <v-avatar size="24" color="grey lighten-4">
                <img
                  src="http://www.myiconfinder.com/uploads/iconsets/256-256-c97dfa83201bd98c5d7ecacba360a5a9-pill.png"
                  alt="avatar"
                />
              </v-avatar>
              <v-flex xs12 pl-3>
                <span class="subheading font-weight-light">{{
                  antimicrobial.antimicrobialName
                }}</span>
              </v-flex>
              <v-flex pl-4>
                <v-icon>keyboard_arrow_right</v-icon>
              </v-flex>
            </v-layout>
          </v-flex>
        </v-layout>
      </v-flex>
    </v-layout>
  </v-container>
</template>

<script>
import { mapActions, mapGetters } from "vuex";

export default {
  created() {
    this.getAntimicrobials(1);
  },
  computed: {
    ...mapGetters(["antimicrobials"]),
    filteredAntimicrobials() {
      return this.antimicrobials.filter(antimicrobial =>
        antimicrobial.antimicrobialName
          .toLowerCase()
          .includes(this.searchAntimicrobe.toLowerCase())
      );
    },
    inputIcon() {
      return this.searchAntimicrobe !== "" ? "clear" : "search";
    }
  },
  data() {
    return {
      searchAntimicrobe: "",
      pageNumber: 1
    };
  },
  methods: {
    ...mapActions(["getAntimicrobials", "setAntimicrobialsPageNumber"]),
    goto(id) {
      this.$router.push(`/antimicrobials/${id}`);
    }
  }
};
</script>

<style></style>
